
@import '../../../scss/breakpoints.scss';


// Mega Menu Sticky
.mega-menu-outer-shopify-wrapper {
    position: sticky;
    top: -1px;  left: 0;
    z-index: 100;
}



// Outer Container Styles
.mega-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;

    z-index: 9999;
    position: relative;
    background-color: #fff;
    box-shadow: 0px 0px 10px #00000029;

    .mega-menu-wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        padding-top: 15px;

        @include to-mdl {
            justify-content: flex-start;
            padding-top: 15px;
            padding-bottom: 5px;
        }
    }

}



// Inner Styles
.mega-menu .mega-menu-wrapper {

    // Bottom Padding Fix
    .store-logo,
    .mobile-control,
    .mega-menu-controls {
        margin-bottom: 10px;
        .burger svg path, .close svg path {
            stroke: var(--primary-color);
        }
        .cross svg path {
            fill: var(--primary-color);
        }
    }

    // Logo Styling
    .store-logo {
        height: auto;  width: clamp( 60px, 15vw, 160px );
        min-width: 90px;
        display: flex;  justify-content: center;  align-items: center;
        margin-right: 40px;

        @include to-mdl {
            margin-right: auto;
            min-width: 60px;
        }

        img {
            width: 100%;  height: 100%;
            object-fit: contain;  object-position: center;
        }
    }


    // Controls Styling
    .mega-menu-controls {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        gap: 0 20px;
        margin-bottom: 0;

        @include to-sm() {
            align-items: flex-start;
        }

        @include to-md() {
            margin-left: auto;
        }

        // Icon styles
        i, a:not(.localization), button:not(.disclosure__button) {
            cursor: pointer;
            display: inline-flex;
            aspect-ratio: 1;
            width: 23px;  height: 23px;

            @include to-mdl {
                width: 18px;
                height: 18px;
            }

            svg {
                width: 100%; height: 100%;
                object-fit: contain;  object-position: center;
                fill: var( --primary-color );
                pointer-events: none;
            }

            &.account {
                svg path {
                    fill: transparent;
                    stroke: var(--primary-color);
                }
            }

            svg.package path {
                fill: transparent;
                stroke: var(--primary-color);
            }

            &.basket {
                color: var(--primary-color);
                position: relative;

                margin-right: 7px;
                &.empty-cart { margin-right: 0px; }


            }


        }

        .account {
            @include to-mdl {
                display: none;
            }
        }

        .search {
            svg {
                width: 23px;  height: 23px;
                fill: var( --primary-color );

                @include to-mdl {
                    width: 18px;
                    height: 18px;
                }
            }
            &.active { display: none; }
            // @include to-md() { display: none; }
        }

        .header-search-bar-wrapper {
            box-sizing: border-box;
            &.active { display: flex; }

            display: none;
            width: clamp(125px, 100%, 180px);

            @include to-sm() {
                position: absolute;
                bottom: -56px;
                left: 0;
                width: 100%;
                justify-content: center;
                align-items: center;
                background: white;
                padding: 10px 0 10px;
                border-top: solid 1px #eee;

                padding: 10px 1.5rem;
            }

            @include to-sm() { padding: 10px 1rem; }
            // @include to-md() { display: none !important; }

        }

        // Search Styles
        .search-bar-qf {
            // display: none;
            background: #F0F0F0;
            border-radius: 2px;
            width: clamp(125px, 100%, 180px);


            @include to-sm() {
                width: 100%;
            }

            &.active { display: flex; }

            input[type=text] {
                width: calc( 100% - 50px );
                padding: 0 40px 0 10px;
                border-radius: 2px;
                background: transparent;
            }

            // Submit
            .submit-group label {
                padding-right: 0;
                svg, svg path { pointer-events: none !important; }
            }
        }

        button:not(.disclosure__button) {
            padding: 0;
        }

        .icon-count {
            position: absolute;
            top: -10px;
            right: -10px;
            width: 20px;
            height: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #818181;
            border-radius: 100%;
            color: #fff;
            font-size: 12px;
        }
    }

    // Mobile Controls styling
    .mobile-control {
        @include from-mdl {
            display: none;
        }

        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 22px;
        @media screen and ( max-width: 320px ) { margin-right: auto }

        appearance: none;
        background: transparent;
        border: none;
        outline: none;

        .openMenu { display: flex; }
        .closeMenu { display: none; }

        &.active {
            .openMenu { display: none; }
            .closeMenu { display: flex; }
        }
    }

}



// Link + Dropdown Styles

    // Top Level Link Styles
    .top-level-links-wrapper {
        background-color: #f4f4f4;
        width: 100%;
        .top-level-links {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px 0;

            @include to-mdl {
                display: none;
            }

            .link-group {
                height: 100%;

                .top-level-link:first-of-type a {
                    padding-left: 0;
                }
                .top-level-link:last-of-type {
                    padding-right: 0;
                }

                .top-level-link, .top-level-link a {
                    height: 100%;
                    margin: 0;
                    gap: 0 8px;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    padding: 0 4px;
                    padding-bottom: 10px;
                    margin-bottom: -10px;

                    font-size: 14px;

                    cursor: pointer;
                    color: var( --text-primary-color );
                    text-decoration: none;
                    svg, path { fill: var( --text-primary-color ); }

                    svg {
                        width: 13px;  height: auto;
                        transform: scaleY( 1 );
                        transition: transform 0s ease-out;
                    }

                    &:hover {
                        opacity: 1;
                        text-decoration: none;
                        font-weight: bold;
                        // text-decoration: underline;
                        & ~ .mega-menu-dropdown_outer-wrapper {
                            display: flex;
                            opacity: 1;
                            pointer-events: auto;
                            .mega-menu-dropdown {
                                    opacity: 1;
                                    transform: translate3d( 0px, 0px, 0px );
                                }
                        }

                        svg { transform: scaleY( -1 ) }
                    }

                }
                .mega-menu-dropdown_outer-wrapper:hover {
                    display: flex;
                    opacity: 1;
                    pointer-events: auto;
                    .mega-menu-dropdown { opacity: 1; transform: translate3d( 0px, 0px, 0px );  }
                }
                &:has(.mega-menu-dropdown_outer-wrapper:hover) {
                    .top-level-link { font-weight: bold; }
                    .top-level-link svg { transform: scaleY(-1); }
                }
            }
        }



        // Dropdown + Outer Wrapper Styling
        .top-level-links .link-group .mega-menu-dropdown_outer-wrapper {
            width: 100%;
            z-index: -1;

            // display: none;
            position: absolute;
            top: calc( 100% - 1px );
            left: 0;
            background: white;

            opacity: 0;
            pointer-events: none;

            // box-shadow: 0px 3px 6px #00000029, inset 0px 10px 13px -10px rgba( 0, 0, 0, 0.25 );
            // box-shadow: 0px 10px 10px rgb(0 0 0 / 16%);
            box-shadow: 0 10px 10px rgb(0 0 0 / 16%), inset 0 5px 10px rgb(0 0 0 / 10%);

            // Dropdown Stylings
            .mega-menu-dropdown {
                width: 100%;  height: 100%;
                padding-top: 55px;  padding-bottom: 60px;

                display: grid;
                grid-template-columns: 1fr 3fr;

                opacity: 0;
                transform: translate3d( 0px, 10px, 0px );
                transition: opacity .15s ease-in .25s, transform .2s ease-in .2s;

                box-sizing: border-box;

                .sublinks-container {
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    gap: 2px 0;

                    min-height: 280px;
                    border-right: solid 1px #eee;

                    .dropdown-title {
                        font-size: 24px;
                        font-weight: bold;
                        margin-bottom: 10px;
                    }

                    .sublink { font-size: 16px; }
                }

                .block-links-wrapper {
                    display: grid;
                    grid-template-columns: repeat( 3, 1fr );
                    gap: 0 30px;
                    margin-left: 30px;

                    .block-link {
                        // aspect-ratio: 1 / 1.2;
                        width: 100%;
                        height: 0;
                        padding-top: 120%;

                        overflow: hidden;
                        position: relative;
                        isolation: isolate;

                        background-color: #F0F0F0;
                        transform: scale( 100% );
                        transition: transform .15s cubic-bezier( 0, 0.6, 0.28, 1 );

                        &:hover {
                            opacity: 1;
                            transform: scale( 104% );
                            span i svg {
                                animation: arrow-pop .4s;
                                animation-iteration-count: 1;
                            }
                        }

                        .block-link-title {
                            width: 100%;
                            position: absolute;
                            bottom: 0; left: 0;
                            padding: 15px;

                            color: var( --primary-color );
                            background-color: rgba(255, 255, 255, 0.5);

                            font-weight: 600;
                            display: flex; flex-direction: row;
                            justify-content: space-between; align-items: center;

                            i {
                                overflow: hidden;
                                transform: rotateZ( -90deg );

                                svg {
                                    transform: translate3d( 0, 0, 0 );

                                    path { fill: var( --primary-color ) }
                                }
                            }
                        }

                        img {
                            z-index: -1;
                            position: absolute;
                            top: 0; left: 0;

                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }

            }

        }
    }


@keyframes arrow-pop {
    0% { transform: translate3d( 0, 0, 0 ); }
    50% { transform: translate3d( 0, 200%, 0 ); opacity: 1; }
    51% { transform: translate3d( 0, -200%, 0 ); opacity: 0; }
    51% { transform: translate3d( 0, -200%, 0 ); opacity: 1; }
    100% { transform: translate3d( 0, 0%, 0 ); }

}


// MOBILE MENU STYLES
.mobile-menu-outer-wrapper {
    visibility: hidden;
    &.active { display: flex; }

    @include from-mdl() {
        display: none;
    }

    height: calc( 100vh - 60px );

    pointer-events: none;
    position: absolute;
    top: auto;  left: 0;
    width: 100%;

    // Dark background
    &::before {
        content: "";
        display: block;
        width: 100%; height: 100%;
        position: fixed;
        top: 0;  left: 0;
        background: #707070;
        opacity: 0.73;

        z-index: -1;
        isolation: isolate;

    }

    &.active {
        pointer-events: all;
        visibility: visible;
        width: 100%;

        .mobile-menu {
            translate: 0%;
            transition: 0.2s ease-in-out .2s;
        }

    }

    .mobile-menu {
    background: white;
    height: 100%;
    width: clamp( 360px, 30%, 360px );

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    translate: -100%;
    transition: 0.1s ease-in-out 0s;

    * { box-sizing: border-box; }

    .mobile-menu-top {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 15px 30px;
        width: 100%;

        .store-logo {
        width: auto;  height: 25px;
        overflow: hidden;

        display: flex;
        justify-content: center; align-items: center;

        img { width: 100%; height: 100%; object-fit: contain; }
        }
    }

    .mobile-menu-bottom {
        width: 100%;
        padding: 0 1.5rem;
        margin-bottom: 30px;

        // Search Bar
        .search-bar-qf {
            width: 100%;
            input { box-sizing: border-box; }
        }
    }

    .mobile-menu-navigation {
        width: 100%;
        margin: 20px 0 auto;
        padding: 0 1.5rem;
        max-height: 70vh;
        overflow-y: scroll;

    }



    }
}